.App {
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #E6EAEE;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: flex-start;
  font-size: 18px;
  color: black;
  margin-bottom: 20px;
  text-align: left;
}

.App-link {
  color: #61dafb;
}

.SwitchBoard {
  margin-top: 20px;
  display: flex;
  font-size: 18px;
  flex-direction: column;
  align-items: baseline;
  justify-content: flex-start;
}

.SwitchRow {
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

.col6 {
  width: 150px;
  text-align: right;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.grid-container {
  display: flex;
  width: 100%;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}

.grid-item {
  display: inline-block;
  border: 1px solid black;
  width: 20px;
  padding-top: 8px;
  padding-bottom: 8px;
  background-color:rgb(191, 191, 191);
  margin-left: 20px;
  border-radius: 5px;
  min-width: 50px;
  font-size: 18px;
}

.row {
  margin-top: 20px;
}

.day {
  width: 100px;
  background-color: #E6EAEE;
  border-color: whitesmoke;
  font-size: 18px;
  text-align:right;
}